import React, { useEffect, useState } from 'react';
import ReactWeather, { useVisualCrossing } from 'react-open-weather';

function LocationDetails() {
  const [error, setError] = useState('');
  const [coords, setCoords] = useState({ lat: null, lon: null });
  const [positionChecked, setPositionChecked] = useState(false);
  const [fetchWeather, setFetchWeather] = useState(false);

  useEffect(() => {
    const getPosition = () => {
      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
          position => {
            const { latitude, longitude } = position.coords;
            console.log(`Position obtained: lat ${latitude}, lon ${longitude}`);
            setCoords({ lat: latitude, lon: longitude });
            setPositionChecked(true);
            setFetchWeather(true);  // Trigger weather data fetch
          },
          error => {
            setError('Unable to obtain location');
            setPositionChecked(true);
          },
          { timeout: 10000 }
        );
      } else {
        setError("Geolocation is not supported by this browser.");
        setPositionChecked(true);
      }
    };

    if (!positionChecked) {
      getPosition();
    }
  }, [positionChecked]);

  useEffect(() => {
    if (fetchWeather) {
      console.log(`Fetching weather data for lat ${coords.lat}, lon ${coords.lon}`);
    }
  }, [fetchWeather, coords]);

  const { data, isLoading, errorMessage } = useVisualCrossing({
    key: 'ESD7LZQJYHEPG563B74GXF8EM',
    lat: coords.lat,
    lon: coords.lon,
    lang: 'en',
    unit: 'metric'
  });

  useEffect(() => {
    if (fetchWeather && !isLoading && data) {
      console.log(`Weather data received: ${new Date().toISOString()}`, data);
      setFetchWeather(false);  // Reset fetchWeather to prevent multiple fetches
    }
    if (errorMessage) {
      console.error(`Error message received: ${new Date().toISOString()}`, errorMessage);
      setError(errorMessage);
    }
  }, [data, errorMessage, isLoading, fetchWeather]);

  if (!positionChecked) return <p>Checking position...</p>;
  if (isLoading && fetchWeather) return <p>Loading weather data...</p>;
  if (error) return <p>Error: {error}</p>;

  return (
    <div>
      <h1>Weather Details</h1>
      {data ? (
        <ReactWeather
          isLoading={isLoading}
          errorMessage={errorMessage}
          data={data}
          lang="en"
          locationLabel={data?.location?.name || 'Your Location'}
          unitsLabels={{ temperature: 'C', windSpeed: 'Km/h' }}
          showForecast
        />
      ) : (
        <p>No weather data available.</p>
      )}
    </div>
  );
}

export default React.memo(LocationDetails);
